.topHeaderWrap {
    height: 60px;
}
.topHeaderLeft {
    display: flex;
}
.topHeaderRight {
    margin: 0 10px 0 10px;
    padding: 10px;
}
.topHeaderRight > svg {
    margin: 0 15px;
}
.topHeaderLogo {
    width: 80px;
    margin: 0 0 0 17px;
}
.dividerLine {
    border: 1px solid rgba(255, 255, 255, 0.4);
    height: 24px;
    margin: 0px 1rem;
}
.TopHeaderTitle {
    font-family: 'Calibre-R';
    color: rgb(246, 237, 221);
}